export default {
	data(){
		return {
			info:"",
			userId:""
		}
	},
	onLoad(e){
		this.userId = e.id;
	},
	methods:{
		fail(){
			if(this.info){
				this.$request.POST({
					url:this.$api.apiUrl.POST_V4_USER_CHECK_PASS,
					data:{
						id:this.userId,
						remark:this.info
					}
				}).then(res=>{
					if(res.code == 200){
						uni.showToast({
							title:"已经将您驳回的信息发送给当前审核的用户！",
							success:()=>{
								uni.navigateTo({
									url:"/pages/mys/kecheck/kecheck"
								})
							}
						})
					}
				})
			}else{
				this.$api.msg('驳回信息不能为空！');
			}
		}
	}
}
